@use "../../assets/styles/variables" as *;
@use "../../assets/styles/mixins" as *;

.about {
  overflow: hidden;
  min-height: 90vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5rem 15rem;
  color: #fafafafa;

  @include tablet {
    display: grid;
    min-height: 90vh;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto auto;
    padding: 2rem 2rem;
    text-align: left;
    grid-column-gap: 3%;
    grid-row-gap: 3%;
    margin-bottom: 30rem;
    align-self: start;
  }

  @include mobile {
    display: flex;
    flex-direction: column;
  }
}



.description {
  flex: 1;
  padding-right: 5rem;
  z-index: 2;
  margin-bottom: 30rem;

  @include tablet {
    margin-bottom: 2rem;
    padding: 0;
  }
}

.hide {
  overflow: hidden;

  h2 {
    font-size: 3rem;
    font-weight: 500;
  }

  @include tablet {
    grid-area: 1 / 1 / 2 / 3;
  }
}

.contents {
  p {
    font-size: 1.2rem;
  }
  @include tablet {
    grid-area: 2 / 1 / 3 / 2;
    align-self: start;
  }
}

.image {
  flex: 1;
  overflow: hidden;
  z-index: 2;
  margin-bottom: 25rem;

  @include tablet {
    margin-bottom: 0;
    padding: 0;
  }

  img {
    width: 100%;
    height: 35rem;
    object-fit: cover;
    object-position: right;

    @include tablet {
      width: 100%;
      height: 25rem;
      object-position: center;
      grid-area: 2 / 2 / 3 / 3;
    }

    @include desktop-large {
     object-fit: contain;
     height: 50rem;
    }
  }
}

.button {
  background-color: $color-secondary;
  &:hover {
    background-color: transparent;
  }
}

.icon {
  color: $color-primary-light;
  font-size: 5rem;

  @include tablet {
    font-size: 4rem;
  }
}

.grid {
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-template-rows: repeat(2, auto);
  gap: 1rem 1rem;
  margin-bottom: 3rem;
  margin-top: 2.5rem;
  align-items: center;

  @include tablet {
  }
}

span {
  transition: all 0.5s ease;
  &:hover {
    color: $color-white;
  }
}

a {
  text-decoration: none;
}
