@use "../../assets/styles/variables" as *;
@use "../../assets/styles/mixins" as *;

.projects {
  background: linear-gradient(#1c1e31, #131628);
  min-height: 90vh;
  // overflow: hidden;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto auto;
  grid-column-gap: 2%;
  grid-row-gap: 3%;
  justify-items: stretch;
  align-items: stretch;
  padding: 5rem 5rem;
  margin: auto;

  @include tablet {
    grid-template-columns: 1fr;
  }

  @include mobile {
    padding: 2rem 2rem;
    grid-template-columns: 90vw;
  }

  .project {
    .title {
      display: flex;
      margin: auto;
      justify-content: space-between;
      align-items: center;

      .icon {
        color: $color-white;
        transition: all 0.25s ease-in-out;
        margin: 0.5rem;
        &:hover {
          color: $color-secondary;
        }
      }

      h2 {
        font-size: 2rem;
        font-weight: 600;
      }
    }
    .line {
      height: 0.3rem;
      background: $color-primary-light;
      margin-bottom: 3rem;
    }

    .imgWrapper {
      overflow: hidden;
      position: relative;

      img {
        width: 100%;
        height: 30vh;
        object-fit: contain;
        aspect-ratio: 1/1;
        margin-bottom: 1.2rem;
      }

      &:hover > img {
        filter: blur(4px);
      }
      .content {
        display: none;
        opacity: 0;
        visibility: hidden;
        justify-content: center;
        align-items: center;
        position: absolute;
        inset: 0;

        p {
          font-size: 2rem;
          font-weight: 600;
          text-decoration: none;
          color: $color-secondary;
          text-decoration: underline;
          text-decoration-color: #fd4370;
          text-decoration-thickness: 4px;

          
        }

        .projectLink {
          color: $color-white;
        }
      }

      &:hover > .content {
        display: flex;
        visibility: visible;
        opacity: 1;
        animation: fade 2s;
      }

      @keyframes fade {
        0% {
          opacity: 0;
        }

        100% {
          opacity: 1;
        }
      }
    }
  }

  ul {
    display: flex;
    list-style: none;
    justify-content: flex-start;

    li {
      position: relative;
      color: $color-primary-light;
      margin-right: 2rem;
      background: #292b3e;
      padding: 1rem 1.5rem;

      @include mobile {
        font-size: 0.7rem;
        margin: 1rem;
        padding: 0.5rem 0.5rem;
      }
    }
  }
}

// Animation Frames

.Frame1 {
  position: fixed;
  left: 0;
  top: 10%;
  width: 100%;
  height: 100vh;
  background: #fffebf;
  z-index: 2;
}

.Frame2 {
  background: #ff8efb;
  left: 0;
  top: 10%;
  width: 100%;
  height: 100vh;
