@use "../styles/variables" as *;



@mixin mobile {
  @media (max-width: $breakpoint-mobile) {
    @content;
  }
}


@mixin tablet {
  @media (max-width: $breakpoint-tablet) {
    @content;
  }
}



@mixin desktop-large {
  @media (min-width: $breakpoint-desktop-large) {
    @content;
  }
}