@use "../../assets/styles/variables" as *;

.layout {
  color: $color-white;
}

.headline {
  min-height: 90vh;
  padding-top: 20vh;
  position: relative;
  h2 {
    position: absolute;
    top: 10%;
    left: 50%;
    transform: translate(-50%, -10%);
  }
  img {
    width: 100%;
    height: 100vh;
    object-fit: cover;
  }
}

.line {
  width: 100%;
  background: $color-secondary;
  height: 0.2rem;
  margin: 1rem 0rem;
}

.detailsLayout {
  min-height: 80vh;
  display: flex;
  margin: 5rem 10rem;
  align-items: center;
  justify-content: space-around;
}

.detailsStyle {
  padding: 5rem;
  h3 {
    font-size: 2rem;
  }
}

.text {
  padding: 2rem 0rem;
}

.secondImg {
  min-height: 50vh;
  img {
    width: 100%;
    height: 100vh;
    object-fit: cover;
  }
}

